<template>
  <div class="app">
    <div class="top" ref="top">
      <div
        class="card"
        v-for="(item, index) in cardAll"
        :key="index"
        :id="item.id"
        :style="{ left: item.left, top: item.top }"
        @touchstart="move(item)"
        :ref="item.id"
      >
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="item.icon"></use>
        </svg>
      </div>
    </div>
    <div ref="botton" class="botton">
      <div
        :class="{'scale':item.icon === isIcon}"
        v-for="(item, index) in removeAll"
        :key="index"
        :style="{ left: item.left }"
        :ref="item.id"
        class="card"
      >
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="item.icon"></use>
        </svg>
      </div>
    </div>
    <button @click="restart">重新开始</button>
    <div class="end" v-if="isMask">
      <span> {{ result }}</span>
      <button @click="restart">重新开始</button>
    </div>
  </div>
</template>

<script>
import { nextTick } from "vue";
export default {
  name: "app",
  mounted() {
    this.createCard();
  },
  data() {
    return {
      icon: [
        { icon: "#icon-shafaB" },
        { icon: "#icon-yiziB" },
        { icon: "#icon-dayinjiB" },
        { icon: "#icon-xiangjiB" },
        { icon: "#icon-dianfengshanB" },
        { icon: "#icon-dianfanbaoB" },
        { icon: "#icon-shouyinjiB" },
        { icon: "#icon-chuifengjiB" },
        { icon: "#icon-yupenB" },
        { icon: "#icon-dengpaoB" },
      ],
      iconAll: [],
      cardAll: [],
      removeAll: [],
      canMove: true,
      result: "您赢了",
      isMask: false,
      isIcon : ""
    };
  },
  methods: {
    createIconAll() {
      for (let i = 0; i < 18; i++) {
        this.iconAll.push(...this.icon);
      }
      this.iconAll.sort(() => Math.random() - 0.5);
    },
    createCard() {
      this.createIconAll();
      for (let c = 5; c >= 0; c--) {
        for (let i = 0; i < 10; i++) {
          for (let j = 0; j < 7; j++) {
            let pyStep = c % 2 === 0 ? 47.7 / 2 : 0;
            let item = Math.random() > 0.5 && this.iconAll.pop();
            if (item) {
              this.cardAll.push({
                id: `m${c}-${i}-${j}`,
                top: 47.7 * i + pyStep + "rem",
                left: 47.7 * j + pyStep + "rem",
                icon: item.icon,
              });
            }
          }
        }
      }
      this.mask();
    },
    mask() {
      this.cardAll.forEach(async (v) => {
        const arr = v.id
          .substring(1)
          .split("-")
          .map((v) => Number(v));
        const isPy = (arr[0] - 1) % 2 === 0;
        for (let i = arr[0] - 1; i >= 0; i--) {
          const isPyB = (i - 1) % 2 === 0;
          if (isPy === isPyB) {
            this.$nextTick(() => {
              const el = document.querySelector(`#m${i}-${arr[1]}-${arr[2]}`);
              if (el) {
                document.querySelector(`#${v.id}`).classList.add("disabled");
              }
            });
          } else if (!isPy && isPyB) {
            await nextTick();
            const result = [
              `${i}-${arr[1]}-${arr[2]}`,
              `${i}-${arr[1]}-${arr[2] + 1}`,
              `${i}-${arr[1] + 1}-${arr[2]}`,
              `${i}-${arr[1] + 1}-${arr[2] + 1}`,
            ].every((k) => {
              return !document.querySelector(`#m${k}`);
            });
            if (!result) {
              this.$refs[v.id][0].classList.add("disabled");
              break;
            } else {
              this.$refs[v.id][0].classList.remove("disabled");
            }

            //  单数层
          } else if (isPy && !isPyB) {
            await nextTick();
            const result = [
              `${i}-${arr[1]}-${arr[2]}`,
              `${i}-${arr[1]}-${arr[2] - 1}`,
              `${i}-${arr[1] - 1}-${arr[2]}`,
              `${i}-${arr[1] - 1}-${arr[2] - 1}`,
            ].every((k) => {
              return !document.querySelector(`#m${k}`);
            });
            if (!result) {
              this.$refs[v.id][0].classList.add("disabled");
              // document.querySelector(`#${v.id}`).classList.add("disabled");
              break;
            } else {
              this.$refs[v.id][0].classList.remove("disabled");
              //  document.querySelector(`#${v.id}`)
            }
          }
        }
      });
    },
    move(item) {
      let Dom = this.$refs[item.id];
      let ClassName = Dom[0].classList[1];
      let length = this.removeAll.length;
      if (ClassName || !this.canMove || length > 6) {
        return;
      }
      this.canMove = false;
      let t = this.$refs.botton.offsetTop;
      let left = 0;
      if (length) {
        left = length * 47.7;
      }
      Dom[0].style.top = t + "px";
      Dom[0].style.left = left + 5 + "rem";

      Dom[0].transitionNameCount = 0;
      Dom[0].ontransitionend = () => {
        Dom[0].transitionNameCount++;
        if (Dom[0].transitionNameCount === 2) {
          item = { ...item, left: left + "rem" };
          this.removeAll.push(item);
          this.$refs[item.id][0].remove();
          let num = this.removeAll.reduce((n, v) => {
            return v.icon === item.icon ? n + 1 : n;
          }, 0);
          if (num === 3) {
            this.removeAll.forEach( (v) => {
              if (v.icon == item.icon) {
                this.isIcon = item.icon
              }
            });

           setTimeout(()=>{
             this.isIcon = ""
              this.removeAll = this.removeAll
              .filter((v) => v.icon != item.icon)
              .map((v, index) => ({ ...v, left: index * 47.7 + "rem" }));
           },300)
          }
         setTimeout(()=>{
            if (!this.$refs.top.childNodes.length) {
            this.result = "您赢了！";
            this.isMask = true;
          } else if (this.removeAll.length === 7) {
            this.result = "您输了！";
            this.isMask = true;
          }
         },301)

          this.canMove = true;
          this.mask();
        }
      };
    },
    restart() {
      this.iconAll = [];
      this.cardAll = [];
      this.removeAll = [];
      this.isMask = false;
      this.createCard();
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
.app {
  background-color: #e9c2a6;
}
.icon {
  width: 47.7rem;
  height: 47.7rem;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.top {
  position: relative;
  width: 360rem;
  height: 500rem;
  margin: 0 auto;
}
.card {
  position: absolute;
  border: 1px solid #000;
  background-color: #fff;
  transition: all 0.3s;
  z-index: 1;
}
.card:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: background-color 0.2s;
}
.disabled:after {
  background-color: rgba(0, 0, 0, 0.7);
}
.botton {
  width: 350rem;
  height: 47.7rem;
  margin: 60rem auto 0;
  background-color: #ccc;
  position: relative;
  /* z-index: -99; */
}
.end {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  align-items: center;
  font-size: 60rem;
  z-index: 10;
}
.end button {
  width: 100rem;
  height: 50rem;
  border-radius: 20rem;
}
.scale {
  transform: scale(0);
}
</style>